import React from "react"
import Head from '../components/head'
import Layout from "../components/layout"
import Contact from '../styles/contact.module.scss'
import { Link } from 'gatsby'

const SentPage = () => (
    <Layout>
        <Head title="Sent" />
        <div style={{marginLeft: '30%' }}>
            <h1 style={{ color: '#64DEF7', marginTop: `10rem` }}>Thanks For The Message!</h1>
            <p style={{ fontSize: '70px', marginTop : '10rem'}}>Head <Link to="/" style={{ color: '#64DEF7', textDecoration: 'none' }}>Back?</Link></p>
        </div>
    </Layout>
)


export default SentPage